
.swiper {
	&-container {
		&-horizontal {
			& > .swiper {
				&-pagination {
					&-bullets {
						bottom: 6.5rem;
						& .swiper {
							&-pagination {
								&-bullet {

									box-sizing: content-box;
									background-clip: content-box;
									border-radius: 0;
									outline: none;
									width: 3rem;
									padding: 1.5rem 0;
									height: .1rem;
									margin: 0 .5rem;
									background-color: rgba(0,0,0, .2);
									position: relative;
									opacity: 1;
									& .line {
										display: block;
										height: 100%;
										background-color: transparent;
										transition: $time; }
									&:hover {
										& .line {
											background-color: #000; } }
									&-active {
										background-color: rgba(0,0,0, .2);
										& .line {
											background: #000;
											width: 100%;
											animation: progress 10000ms 1 linear; } } } } } } } } } } }

.fancybox {
	&-is-open {
		& .fancybox-bg {
			background: rgba(0,0,0,1);
			opacity: .85; } }
	&-button {
		outline: none;
		transition: opacity 400ms cubic-bezier(.694,0,.335,1),transform 400ms cubic-bezier(.694,0,.335,1),-webkit-transform 400ms cubic-bezier(.694,0,.335,1);
		&--close {
			position: absolute;
			outline: none;
			top: 3rem;
			right: 3rem;
			width: 3rem;
			height: 3rem;
			padding: 0;
			opacity: .75;
			background: none;
			transform: scale(.75);
			&:hover {
				opacity: 1;
				background: none;
				transform: scale(1); } }
		& svg {
			outline: none;
			margin-right: 0;
			display: block;
			width: 100%;
			height: 100%;
			stroke: none;
			& line {
				stroke: #fff;
				fill: none; } } } }


.no-gutters {
	margin-right: 0;
	margin-left: 0;
	> .col, > [class*="col-"] {
		padding-right: 0;
		padding-left: 0; } }
