  transition: color 100ms cubic-bezier(0.694, 0, 0.335, 1), font-weight 100ms cubic-bezier(0.694, 0, 0.335, 1);
  font-weight: 300;
}

.dropdown__menu-link:hover {
  font-weight: 400;
  color: #222;
}

.dropdown__menu.is--active {
  transform: translate3d(0, 1rem, 0);
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  display: block;
  transition: opacity 300ms cubic-bezier(0.694, 0, 0.335, 1), visibility 300ms step-start, transform 300ms cubic-bezier(0.694, 0, 0.335, 1);
}

.dropdown__menu.is--active .dropdown__menu-caret {
  top: 0;
}

.links {
  max-width: 100%;
  width: 100%;
}

.arrow-down {
  width: 1.4rem;
  fill: #000;
  float: right;
  display: inline-block;
  vertical-align: middle;
  transition: transform .3s ease;
  opacity: 1;
}
